<template>
    <div>
        <h4>Payment</h4>

        <p>
            Please select the preferred payment method to use on this order.
        </p>

        <el-row :gutter="20">
            <el-col>
                <Radio v-model="payment" label="1" border>PayPal</Radio>
                <Tooltip content="We are not supporting credit cards at the moment, but will be soon">
                    <Radio v-model="payment" label="2" border disabled>Credit Card</Radio>
                </Tooltip>
            </el-col>
        </el-row>

        <h4>Account</h4>

        <p>
            Please provide account name and password for your booster.
        </p>
        <Form ref="account_details"
              :model="account_details"
              label-position="top"
              :rules="rules"
              status-icon
              labwidth="120px">
            <el-row :gutter="20">
                <el-col :span="12">
                    <FormItem auto-complete="off" prop="account_name">
                        <el-input v-model="account_details.account_name" placeholder="Account Name"/>
                    </FormItem>
                </el-col>

                <el-col :span="12">
                    <FormItem auto-complete="off" prop="password">
                        <el-input type="password" v-model="account_details.password" placeholder="Password"/>
                    </FormItem>
                </el-col>
            </el-row>
        <h4>Contacts</h4>

        <p>
            Once you pay how would you like to be contacted by our company to assign the booster?
        </p>
        <el-row :gutter="20">
            <el-col>
                <div>
                    <el-checkbox-group v-model="contacts" size="small">
                        <el-checkbox v-model="contact.discord" size="small" label="Discord" border/>
                        <el-checkbox v-model="contact.contact_email"   size="small" label="E-Mail" border/>
                        <el-checkbox v-model="contact.skype"   size="small" label="Skype" border/>
                    </el-checkbox-group>
                </div>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col>
                <br>
                <FormItem label="Discord">
                    <el-input v-if="includes('Discord')" v-model="account_details.discord" size="small" placeholder="Discord account name" />
                </FormItem>
                <FormItem label="E-Mail">
                    <el-input v-if="includes('E-Mail')" v-model="account_details.contact_email" size="small" :placeholder="contactEmail" />
                </FormItem>
                <FormItem label="Skype">
                    <el-input v-if="includes('Skype')" v-model="account_details.skype" size="small" placeholder="Skype account name" />
                </FormItem>
            </el-col>
        </el-row>
        </Form>
    </div>
</template>

<script>
import { Radio, Row, Col, Checkbox, CheckboxGroup, Form, FormItem, Input, Tooltip } from 'element-ui';

export default {
  name: 'Payment',
  components: {
    'el-col': Col,
    'el-row': Row,
    'el-checkbox': Checkbox,
    'el-checkbox-group': CheckboxGroup,
    'el-input': Input,
    Form,
    FormItem,
    Radio,
    Tooltip,
  },
  data() {
    return {
      payment: '1',
      contacts: [],
      account: {
        discord: '',
        skype: '',
        contact_email: '',
      },
      account_details: {
        account_name: '',
        password: '',
      },
      contact: {
        discord: false,
        skype: false,
        contact_email: false,
      },
      rules: {
        account_name: [
          {
            type: 'string', whitespace: true, required: true, message: 'Account Name is required', trigger: 'blur',
          },
        ],
        password: [
          {
            type: 'string', whitespace: true, required: true, message: 'Password is required', trigger: 'blur',
          },
        ],
      },
    };
  },
  computed: {
    contactEmail() {
      return `E-Mail: ${this.$store.getters['user/data'].email}`;
    },
    // discord() { console.log(this.contacts); return this.contacts.includes('Discord'); },
    // skype() { return this.contacts.includes('Skype'); },
    // email() { return this.contacts.includes('E-Mail'); },
  },
  methods: {
    includes(type) {
      return this.contacts.includes(type);
    },
  },
};
</script>

<style lang="less">
    hr {
        border-top: 1pt solid #d8dbe2;
        border-bottom: none;
        border-left: none;
        border-right: none;
    }
